.home-container{
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.home-right{
  padding: 10px 8px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.home-right > h1{
  font-size: 3rem;
  font-weight: 600;
  text-overflow: clip;
  text-align: center;
  margin-bottom: 12px;
}
.home-right > p {
  margin: 8px;
}

.home-right button{
  min-width: 200px;
  width: max-content;
  height: 150x;
  padding: 12px 20px;
  font-size: 1.8rem;
  margin: 19px auto;
  color: black;
  border-radius: 12px;
  justify-self: center;
  align-self: center;
  border: 2px solid black;
}
.home-right button :hover{
  color: white;
}

@media (width < 800px) {
  .home-container{
    grid-template-columns: 1fr;
  }
}
